body {
  margin: 0;
  font-family: 'Montserrat', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  line-height: 1.8em !important;
  font-size: 18px;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  font-weight: 700 !important;
  color: #ff04b4!important;
  text-decoration: none!important;
  cursor: pointer;
}

p {
  text-align: justify;
}

.container {
  max-width: 1170px;
  padding: 0 50px;
}

.text_block {
  margin-bottom: 40px;
  margin-top:40px;
}

.footer {
  position: fixed;
  width: 100%;
  height: 60px; /* Set the fixed height of the footer here */
  line-height: 60px; /* Vertically center the text there */
  background-color: #f5f5f5;
  bottom: 1px;

}

footer a {
  font-size: 20px;
}

footer a:first-child {
  margin-right:40px;
  margin-left:-20px;
}

.btn-primary {
  font-family: 'Montserrat', sans-serif !important;
  font-weight: 800;
  letter-spacing: 2px;
  background-color: #ff04b4;
  border-color: #e1009e;
  margin-bottom: 40px;
}

.btn-primary:hover {
  background-color: #ae057a;
  border-color: #940469;
}
.btn-primary:active,.btn-primary:focus {
  background-color: #880462;
  border-color: #750454;
}
